<template>
    <v-app>
        <toolbar  :title="title" :show="true"></toolbar>
         <v-btn color="blue" @click="$router.push('/')" outline>Back to main page</v-btn>
             <v-layout row wrap mx-2 mt-2>
                  <div v-for="i in 9" :key="i" class="px-1" style="overflow:hidden;">
                   <img src="https://i.imgur.com/Z2JGqfk.jpg" height="240px" >
                   <p style="font-size:20px">Inarugation function</p>
                     
                  </div>
                 
             </v-layout>
        <fv></fv>
        <footnav></footnav>
    </v-app>
</template>

<script>
import toolbar from './toolbar.vue'
import fv from './fv'
import footnav from './footnav.vue'
export default {
  components:{
    toolbar,fv,footnav
  },
  data(){
      return{
          title: 'CSE DEPARTMENT',
      }}
}
</script>



