<template>
    <v-app>
        <toolbar  :title="title" :show="true"></toolbar>
        <v-container>
            <v-btn color="blue" outline round @click="$router.push('/events')"><v-icon class="pr-1">replay</v-icon> Back To Event Page </v-btn>
        </v-container>
        <v-container>
            <v-img src="http://knowafest.com/files/uploads/Avantaa-2018030906.png" ></v-img>
            <h2>Avantaa</h2>
            <p>I take immense pleasure in inviting you all on behalf of 'sri krishna college of technology' to the avantta held on 11.03.2017 ..</p>
        </v-container>
        
        
    </v-app>
</template>

<script>
import toolbar from './toolbar.vue'
import footnav from './footnav.vue'
import 'bootstrap'
import "bootstrap/dist/css/bootstrap.min.css"
import fv from './fv'






export default {
    components:{
        toolbar,fv,footnav
    },
    data(){
        return{
            title: 'CSE DEPARTMENT',
        }}

}
</script>


<style scoped>




h2 {
	color: #000;
	font-size: 26px;
	font-weight: 300;
	text-align: center;
	text-transform: uppercase;
	position: relative;
	margin: 30px 0 70px;
}
h2::after {
	content: "";
	width: 100px;
	position: absolute;
	margin: 0 auto;
	height: 4px;
	border-radius: 1px;
	background: #1c47e3;
	left: 0;
	right: 0;
	bottom: -20px;
}
.carousel {
	margin: 50px auto;
	padding: 0 70px;
}
.carousel .item {
	color: #999;
	overflow: hidden;
    min-height: 120px;
	font-size: 13px;
}
.carousel .media img {
	width: 80px;
	height: 80px;
	display: block;
	border-radius: 50%;
}
.carousel .testimonial {
	padding: 0 15px 0 60px ;
	position: relative;
}
.carousel .testimonial::before {
	content: '\93';
	color: #e2e2e2;
	font-weight: bold;
	font-size: 68px;
	line-height: 54px;
	position: absolute;
	left: 15px;
	top: 0;
}
.carousel .overview b {
	text-transform: uppercase;
	color: #1c47e3;
}
.carousel .carousel-indicators {
	bottom: -40px;
}
.carousel-indicators li, .carousel-indicators li.active {
	width: 18px;
    height: 18px;
	border-radius: 50%;
	margin: 1px 3px;
}
.carousel-indicators li {	
    background: #e2e2e2;
    border: 4px solid #fff;
}
.carousel-indicators li.active {
	color: #fff;
    background: #1c47e3;    
    border: 5px double;    
}


</style>



