<script>
import toolbar from './toolbar.vue'
import fv from './fv'
import 'bootstrap'
import "bootstrap/dist/css/bootstrap.min.css"
import footnav from './footnav.vue'
import * as easings from 'vuetify/es5/util/easing-patterns'
import countTo from 'vue-count-to'
import ICountUp from 'vue-countup-v2'
import 'swiper/dist/css/swiper.css'
import 'swiper/dist/js/swiper.js'
import {swiper,swiperSlide} from 'vue-awesome-swiper'
import pdf from 'vue-pdf'

export default {
    components: {
     toolbar,footnav,fv,ICountUp,countTo,swiper,swiperSlide,pdf
  },

   data(){
       return{
         swiperOption:{
           grapCursor:true,
           centeredSlides: true,
           slidesPerView: 'auto',
           autoplay: {
             delay: 2000,
             disableOnInteraction: false,
           } 
         },
         
         title: '- CSE DEPARTMENT',
           

    e1:9,

    e2:32,

    e3:84,

    e4:400,

    delay: 20000,

    currentNumber: 0,

    timer: null,

    o1: {

          useEasing: true,

          useGrouping: true,

          separator: ',',

          decimal: '.',

          prefix: '',

          suffix: '+',

       

        },

    o2: {

          useEasing: true,

          useGrouping: true,

          separator: ',',

          decimal: '.',

          prefix: '',

          suffix:'+',

       

        },

    o3: {

          useEasing: true,

          useGrouping: true,

          separator: ',',

          decimal: '.',

          prefix: '',

          suffix:'+',

       

        },

    o4: {

          useEasing: true,

          useGrouping: true,

          separator: ',',

          decimal: '.',

          prefix: '',

          suffix:'+',
        },
         ex:[
           { title : "Achievements",sub:['Department','Faculty','Student']},
           { title: "Placement Statitcs",sub:['2014-2015','2015-2016','2016-2017','2017-2018','2018-2019','2019-2020']}
           ],
      items3: [
          {
            src: 'http://skct.edu.in/SKCT-CSE/images/slider/3.jpg'
          },
          {
            src: 'http://skct.edu.in/SKCT-CSE/images/slider/4.jpg'
          },
          {
            src: 'http://skct.edu.in/SKCT-CSE/images/slider/5.jpg'
          },
          {
            src: 'http://skct.edu.in/SKCT-CSE/images/slider/4.jpg'
          }
        ],
        value:0,
        value1: 0,
        interval1:{},
        interval:{},
        show: true,

}

},

mounted () {
      this.interval = setInterval(() => {
        if (this.value === 714) {
           isPaused;
        }
        this.value += 2
      }, 60),
       this.interval1 = setInterval(() => {
        if (this.value1 === 37) {
           isPaused;
        }
        this.value1 += 1
      }, 70)
      } ,
   }
  

</script>

